
.aside{
  position: fixed;
  right: 0;
  top: 0;

  width: 220px;
  background: #fff;

  z-index: -1;
  display: none;

  &.active{
    z-index: 8;
    display: block;
  }

  &-list{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__wrapper{
    padding: 50px 30px 75px 30px;

    display: flex;
    flex-direction: column;

    height: 100vh;

    min-height: 500px;
  }

  &__footer{
    margin-top:auto;
  }

  &__btn{
    font-weight: 600;
    font-size: 14px;
    letter-spacing: 0.02em;

    color: #2956FF;

    border: 1px solid #2956FF;
    border-radius: 25px;
    width: 100%;
    max-width: 160px;

    margin: 0 auto;
    height: 50px;
  }

  &-list{
    &__item + &__item{
      margin: 20px 0 0 0;
    }
    &__item{
      &.active{
        .aside-list{
          z-index: 8;
          display: flex;
        }
      }
      .aside-list{
        display: none;
      }
    }
    &__link{
      font-weight: 600;
      font-size: 16px;
      line-height: 20px;
      color: #2A2A2A;
    }
    &__item{
      flex-wrap: wrap;

      .aside-list{
        flex: 100%;
        order: 3;
      }
      .aside-list .aside-list__link{
        font-weight: 400;
      }
    }
    &__link + .aside-nav__list{
      margin-top: 25px;
    }
  }
}

.burger{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  width: 18px;
  height: 12px;
  position: relative;
  &__item{
    flex-shrink: 0;

    width: 100%;
    height: 2px;
    border-radius: 3px;
    background: #1b1b1b;

    transition: 0.5s all;
  }
  &:hover{
    cursor: pointer;
  }
  &.active{
    transform: translateY(-2px);

    position: fixed;
    z-index: 9;
    .burger{
      &__item{
        position: absolute;
        &:nth-child(2){
          display: none;
        }
        &:nth-child(1){
          left: 0;
          top: 0;

          transform: rotate(45deg);
        }
        &:nth-child(3){
          right: 0;
          top: 0;

          transform: rotate(-45deg);
        }
      }
    }
  }
}



@media screen and (max-width: 600px) {
  .aside{
    width: 100%;

    top: 45px;
    &__wrapper{
      padding: 50px 0 75px 0;
    }
    &-list{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;

      &__item{
        width: 100%;

        &.header-list__item-has-child{
          position: relative;
        }
      }
      &__link{
        width: 100%;

        display: flex;

        padding-left: calc(50% - 25vw)
      }
    }
  }
}