*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Acrom';
  src: url("../fonts/Acrom/Acrom-Medium/Acrom-Medium.woff2") format("woff2"), url("../fonts/Acrom/Acrom-Medium/Acrom-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Acrom';
  src: url("../fonts/Acrom/Acrom-Regular/Acrom-Regular.woff2") format("woff2"), url("../fonts/Acrom/Acrom-Regular/Acrom-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Acrom';
  src: url("../fonts/Acrom/Acrom-Bold/Acrom-Bold.woff2") format("woff2"), url("../fonts/Acrom/Acrom-Bold/Acrom-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather/Merriweather-Bold/Merriweather-Bold.woff2") format("woff2"), url("../fonts/Merriweather/Merriweather-Bold/Merriweather-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/Montserrat-SemiBold/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-SemiBold/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/Montserrat-Regular/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Regular/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

body.no-scroll, html.no-scroll {
  overflow: hidden;
}

body {
  position: relative;
  font-family: 'Montserrat', sans-serif;
}

body.no-scroll {
  touch-action: none;
  -ms-touch-action: none;
  height: 100%;
  width: 100%;
  position: fixed;
}

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-small {
  max-width: 900px;
}

.btn {
  -webkit-appearance: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.btn-purple {
  border-radius: 5px;
  background-color: #ff8c82;
  font-family: 'Acrom', sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: #fefefe;
}

.btn-light-blue {
  background: #D8E9FE;
  border-radius: 8px;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #858585;
}

.btn-blue {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #FFFFFF;
  background: #4060FA;
  border-radius: 8px;
}

.btn-transparent {
  border: 1px solid #1b1b1b;
  border-radius: 5px;
  width: max-content;
  height: 30px;
  padding: 0 10px;
}

.page-wrapper {
  display: flex;
  background: #F5F5FB;
  min-height: 100vh;
}

.page-wrapper__aside {
  position: sticky;
  width: 216px;
  height: 100vh;
  top: 0;
  left: 0;
}

.page-wrapper__content {
  padding: 50px 15px;
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
}

.page-wrapper__banner {
  margin: 80px 0 80px 0;
}

.page-wrapper__block + .page-wrapper__block {
  margin-top: 30px;
}

.page-wrapper__block-title {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
  font-family: 'Montserrat', sans-serif;
}

.trigger-block {
  display: none;
}

.trigger-block.active {
  display: block;
}

@media screen and (max-width: 991px) {
  .page-wrapper__aside {
    position: fixed;
    z-index: 99;
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 700px) {
  .page-wrapper__content {
    padding: 30px 16px 60px 16px;
  }
}

@media screen and (min-width: 600px) {
  .page-wrapper__block-title {
    display: none;
  }
}

.premium-text::after {
  content: '';
  width: 15px;
  height: 15px;
  display: inline-block;
  margin-left: 5px;
  transform: translateY(2px);
  background: url("../img/crown.png") center center/contain no-repeat;
}

.header__wrapper {
  display: flex;
  align-items: center;
  height: 45px;
}

.header__nav {
  margin-left: auto;
}

.header__burger {
  margin-left: 20px;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-list__item {
  position: relative;
}

.header-list__item + .header-list__item {
  margin-left: 30px;
}

.header-list__item-has-child {
  display: flex;
  align-items: center;
}

.header-list__item-has-child.active .header-list {
  z-index: 8;
  display: flex;
}

.header-list__item-has-child > .header-list__link {
  display: flex;
  align-items: center;
}

.header-list__item-has-child > .header-list__link::after {
  content: '';
  width: 10px;
  height: 5px;
  display: block;
  margin-left: 5px;
  background: url("../img/arrow.svg") center center/contain no-repeat;
}

.header-list__item .header-list {
  position: absolute;
  left: calc(50% - 55px);
  top: 30px;
  flex-direction: column;
  padding: 10px 30px;
  width: 110px;
  background: #FFFFFF;
  box-shadow: 0px 3px 15px rgba(26, 43, 52, 0.1);
  border-radius: 8px;
  display: none;
  z-index: -1;
}

.header-list__item .header-list.active {
  z-index: 8;
  display: flex;
}

.header-list__item .header-list .header-list__item + .header-list__item {
  margin: 12px 0 0 0;
}

.header-list__item .header-list .header-list__link {
  font-weight: 500;
}

.header-list__link {
  font-size: 14px;
  font-weight: 700;
  color: #000000;
  display: block;
  position: relative;
}

.header-list__link::before {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #2956FF;
  opacity: 0;
  transition: .3s opacity;
}

.header-list__link:hover::before {
  opacity: 1;
}

.logo__text {
  font-weight: 700;
  font-size: 16px;
}

@media screen and (max-width: 600px) {
  .header__nav {
    display: none;
  }
  .header__burger {
    margin-left: auto;
  }
}

.aside {
  position: fixed;
  right: 0;
  top: 0;
  width: 220px;
  background: #fff;
  z-index: -1;
  display: none;
}

.aside.active {
  z-index: 8;
  display: block;
}

.aside-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.aside__wrapper {
  padding: 50px 30px 75px 30px;
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: 500px;
}

.aside__footer {
  margin-top: auto;
}

.aside__btn {
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.02em;
  color: #2956FF;
  border: 1px solid #2956FF;
  border-radius: 25px;
  width: 100%;
  max-width: 160px;
  margin: 0 auto;
  height: 50px;
}

.aside-list__item + .aside-list__item {
  margin: 20px 0 0 0;
}

.aside-list__item.active .aside-list {
  z-index: 8;
  display: flex;
}

.aside-list__item .aside-list {
  display: none;
}

.aside-list__link {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #2A2A2A;
}

.aside-list__item {
  flex-wrap: wrap;
}

.aside-list__item .aside-list {
  flex: 100%;
  order: 3;
}

.aside-list__item .aside-list .aside-list__link {
  font-weight: 400;
}

.aside-list__link + .aside-nav__list {
  margin-top: 25px;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  width: 18px;
  height: 12px;
  position: relative;
}

.burger__item {
  flex-shrink: 0;
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background: #1b1b1b;
  transition: 0.5s all;
}

.burger:hover {
  cursor: pointer;
}

.burger.active {
  transform: translateY(-2px);
  position: fixed;
  z-index: 9;
}

.burger.active .burger__item {
  position: absolute;
}

.burger.active .burger__item:nth-child(2) {
  display: none;
}

.burger.active .burger__item:nth-child(1) {
  left: 0;
  top: 0;
  transform: rotate(45deg);
}

.burger.active .burger__item:nth-child(3) {
  right: 0;
  top: 0;
  transform: rotate(-45deg);
}

@media screen and (max-width: 600px) {
  .aside {
    width: 100%;
    top: 45px;
  }
  .aside__wrapper {
    padding: 50px 0 75px 0;
  }
  .aside-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
  .aside-list__item {
    width: 100%;
  }
  .aside-list__item.header-list__item-has-child {
    position: relative;
  }
  .aside-list__link {
    width: 100%;
    display: flex;
    padding-left: calc(50% - 25vw);
  }
}

.profile__wrapper {
  width: 100%;
  max-width: 780px;
  margin: 0 auto;
  overflow: hidden;
}

.profile-wrapper__block {
  width: 100%;
  background: #FFFFFF;
  border-radius: 8px;
  position: relative;
  padding: 15px 23px;
}

.profile-wrapper__block + .profile-wrapper__block {
  margin-top: 15px;
}

.profile-block__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.profile-block__header-title {
  color: #888888;
  font-size: 16px;
}

.profile-block__header.trigger-link::after {
  content: '';
  width: 15px;
  height: 15px;
  display: block;
  transform: rotate(180deg);
  background: url("../img/block-arrow.png") center center/contain no-repeat;
}

.profile-block__header.active::after {
  transform: rotate(0);
}

.profile-block__header + .profile-block__content {
  margin-top: 30px;
}

.profile-block__content-title {
  font-weight: 700;
  color: #202020;
  font-size: 18px;
  margin-bottom: 10px;
}

.profile-block__content-text {
  color: #202020;
  font-size: 16px;
}

.profile-block__content-text + .profile-block__content-text {
  margin-top: 5px;
}

.profile-block__content-text + .profile-block__content-btn {
  margin-top: 15px;
}

.profile-block__content-btn + .profile-block__content-text {
  margin-top: 15px;
}

.profile-block__content-btn {
  max-width: 220px;
  height: 30px;
}

.profile-block__content-block + .profile-block__content-block {
  margin-top: 30px;
}

.profile-block__input-content {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.profile-block__input-item {
  border: 1px solid #a7a7a7;
  border-radius: 5px;
  margin: 0;
  outline: none;
  height: 25px;
  width: 180px;
  padding: 15px;
}

.profile-block__input-status {
  margin-left: 10px;
  font-size: 14px;
  display: flex;
  align-items: center;
}

.profile-block__input-status::before {
  content: '';
  width: 15px;
  height: 15px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  margin-right: 10px;
}

.profile-block__input-status_success {
  color: #127d12;
}

.profile-block__input-status_success::before {
  background-image: url("../img/success-pass.png");
}

.profile-block__input-status_error {
  color: #d32f2e;
}

.profile-block__input-status_error::before {
  background-image: url("../img/error-pass.png");
}

.profile-table {
  border: 2px solid #dddddd;
  border-radius: 10px;
  width: max-content;
}

.profile-table__row {
  display: grid;
  grid-template-columns: 120px 220px 120px 120px;
  border-bottom: 2px solid #dddddd;
}

.profile-table__row:last-child {
  border-bottom: none;
}

.profile-table__column {
  font-size: 16px;
  padding: 10px 15px;
  border-left: 2px solid #dddddd;
}

.profile-table__column:first-child {
  border-left: none;
}

@media screen and (max-width: 767px) {
  .profile-table {
    width: 100%;
    overflow: hidden;
  }
  .profile-table__row {
    overflow: scroll;
  }
  .profile-table__column {
    font-size: 14px;
  }
}

@media screen and (max-width: 600px) {
  .profile-block__input-item {
    width: 150px;
  }
  .profile-block__input-status {
    font-size: 12px;
  }
  .profile-block__input-status::before {
    width: 10px;
    height: 10px;
    margin-right: 5px;
  }
  .profile-table__row {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
  .profile-table__column {
    padding: 5px;
    text-align: center;
    font-size: 12px;
  }
}
