$min_width: 375px;
$max_width: 1920px;


@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      font-size: $min-font-size;
      @media screen and (min-width: $min-vw) {
        font-size: calc(#{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
      }
      @media screen and (min-width: $max-vw) {
        font-size: $max-font-size;
      }
    }
  }
}



body,html{
  &.no-scroll{
    overflow: hidden
  }
}

body{
  position: relative;
  font-family: 'Montserrat', sans-serif;
  &.no-scroll{
    touch-action: none;
    -ms-touch-action: none;
    height: 100%;
    width: 100%;

    position: fixed;
  }
}

.container{
  max-width: 1230px;

  margin: 0 auto;

  padding: 0 15px;

  &-small{
    max-width: 900px;
  }
}




.btn{
  -webkit-appearance: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;


  &-purple{
    border-radius: 5px;

    background-color: #ff8c82;

    font-family: 'Acrom', sans-serif;
    font-weight: 500;
    font-size: 18px;
    color:#fefefe;
  }

  &-light-blue{
    background: #D8E9FE;
    border-radius: 8px;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    letter-spacing: -0.01em;
    color: #858585;

  }
  &-blue{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 14px;

    letter-spacing: -0.01em;

    color: #FFFFFF;

    background: #4060FA;
    border-radius: 8px;
  }
  &-transparent{

    border: 1px solid #1b1b1b;

    border-radius: 5px;

    width: max-content;
    height: 30px;
    padding: 0 10px;
  }
}

.page-wrapper{
  display: flex;

  background: #F5F5FB;

  min-height: 100vh;
  &__aside{
    position: sticky;
    width: 216px;
    height: 100vh;

    top: 0;
    left: 0;
  }
  &__content{
    padding: 50px 15px;

    width: 100%;
    max-width: 1230px;

    margin: 0 auto;
  }
  &__banner{
    margin: 80px 0 80px 0;
  }
  &__block + &__block{
    margin-top: 30px;
  }
  &__block{
    &-title{
      margin-bottom: 10px;

      font-size: 16px;
      font-weight: 600;
      font-family: 'Montserrat', sans-serif;
    }
  }
}

.trigger{
  &-block{
    display: none;
    &.active{
      display: block;
    }
  }
}

@media screen and (max-width: 991px) {
  .page-wrapper{
    &__aside{
      position: fixed;
      z-index: 99;

      width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 700px) {
  .page-wrapper{
    &__content{
      padding: 30px 16px 60px 16px;
    }
  }
}


@media screen and (min-width: 600px) {
  .page{
    &-wrapper{
      &__block{
        &-title{
          display: none;
        }
      }
    }
  }
}


.premium-text{
  &::after{
    content: '';

    width: 15px;
    height: 15px;
    display: inline-block;

    margin-left: 5px;

    transform: translateY(2px);

    background: url("../img/crown.png") center center/contain no-repeat;
  }
}