.header{
  &__wrapper{
    display: flex;
    align-items: center;
    height: 45px;
  }
  &__nav{
    margin-left: auto;
  }
  &__burger{
    margin-left: 20px;
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
  }
  &-list{
    &__item{
      position: relative;
      & + &{
        margin-left: 30px;
      }
      &-has-child{
        display: flex;
        align-items: center;
        &.active{
          .header-list{
            z-index: 8;
            display: flex;
          }
        }
        &>.header-list__link{
          display: flex;
          align-items: center;
          &::after{
            content: '';

            width: 10px;
            height: 5px;
            display: block;

            margin-left: 5px;

            background: url("../img/arrow.svg") center center/contain no-repeat;
          }
        }
      }
      .header{
        &-list{
          position: absolute;
          left: calc(50% - 55px);
          top: 30px;

          flex-direction: column;

          padding: 10px 30px;
          width: 110px;
          background: #FFFFFF;
          box-shadow: 0px 3px 15px rgba(26, 43, 52, 0.1);
          border-radius: 8px;
          display: none;
          z-index: -1;
          &.active{
            z-index: 8;
            display: flex;
          }
          .header-list__item + .header-list__item{
            margin: 12px 0 0 0;
          }
          .header{
            &-list{
              &__link{
                font-weight: 500;
              }
            }
          }
        }
      }
    }
    &__link{
      font-size: 14px;
      font-weight: 700;

      color:#000000;
      display: block;
      position: relative;
      &::before{
        content: '';

        position: absolute;
        bottom: -5px;
        left: 0;
        width: 100%;
        height: 2px;
        background: #2956FF;

        opacity: 0;
        transition: .3s opacity ;
      }
      &:hover::before{
        opacity: 1;
      }
    }
  }
}

.logo{
  &__text{
    font-weight: 700;
    font-size: 16px;
  }
}


@media screen and (max-width: 600px) {
  .header{
    &__nav{
      display: none;
    }
    &__burger{
      margin-left: auto;
    }
  }
}